import { createElement } from '../render.js';

// Функция для создания шаблона информации о поездке
const createTripInfoTemplate = () => `
  <section class="trip-main__trip-info  trip-info">
    <div class="trip-info__main">
      <h1 class="trip-info__title">Amsterdam &mdash; Chamonix &mdash; Geneva</h1>

      <p class="trip-info__dates">18&nbsp;&mdash;&nbsp;20 Mar</p>
    </div>

    <p class="trip-info__cost">
      Total: &euro;&nbsp;<span class="trip-info__cost-value">1230</span>
    </p>
  </section>`;

// Функция для создания шаблона точки маршрута
const createTripEventsPointTemplate = () => `
  <li class="trip-events__item">
    <div class="event">
      <time class="event__date" datetime="2019-03-18">MAR 18</time>
      <div class="event__type">
        <img class="event__type-icon" width="42" height="42" src="img/icons/taxi.png" alt="Event type icon">
      </div>
      <h3 class="event__title">Taxi Amsterdam</h3>
      <div class="event__schedule">
        <p class="event__time">
          <time class="event__start-time" datetime="2019-03-18T10:30">10:30</time>
          &mdash;
          <time class="event__end-time" datetime="2019-03-18T11:00">11:00</time>
        </p>
        <p class="event__duration">30M</p>
      </div>
      <p class="event__price">
        &euro;&nbsp;<span class="event__price-value">20</span>
      </p>
      <h4 class="visually-hidden">Offers:</h4>
      <ul class="event__selected-offers">
        <li class="event__offer">
          <span class="event__offer-title">Order Uber</span>
          &plus;&euro;&nbsp;
          <span class="event__offer-price">20</span>
        </li>
      </ul>
      <button class="event__favorite-btn event__favorite-btn--active" type="button">
        <span class="visually-hidden">Add to favorite</span>
        <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
          <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
        </svg>
      </button>
      <button class="event__rollup-btn" type="button">
        <span class="visually-hidden">Open event</span>
      </button>
    </div>
  </li>`;

export default class RoutePointView {
  // Метод для получения шаблона информации о поездке
  getTripInfoTemplate() {
    return createTripInfoTemplate();
  }

  // Метод для получения шаблона точки маршрута
  getTripEventsPointTemplate() {
    return createTripEventsPointTemplate();
  }

  // Метод для получения элемента информации о поездке
  getTripInfoElement() {
    if (!this.tripInfoElement) {
      this.tripInfoElement = createElement(this.getTripInfoTemplate());
    }

    return this.tripInfoElement;
  }

  // Метод для получения элемента точки маршрута
  getTripEventsPointElement() {
    if (!this.tripEventsPointElement) {
      this.tripEventsPointElement = createElement(this.getTripEventsPointTemplate());
    }

    return this.tripEventsPointElement;
  }

  // Метод для удаления элемента информации о поездке
  removeTripInfoElement() {
    this.tripInfoElement = null;
  }

  // Метод для удаления элемента точки маршрута
  removeTripEventsPointElement() {
    this.tripEventsPointElement = null;
  }
}
